/**
 * 1. mixin
 * 2.  this.checkVersion() 
 * 3. 
 * 4. 
 * 5. 
 */
import { checkUpdate, downloadUpdate, installUpdate } from '@/utils/updateApp'
export default {
  methods: {
    checkVersion() {
      // 
      checkUpdate()
        .then(res => {
          console.log(':', res.hasUpdate)
          if (res.hasUpdate) {
            uni.showModal({
              title: '',
              content: '',
              success: res => {
                if (res.confirm) {
                  // 
                  downloadUpdate()
                    .then(() => {
                      uni.showToast({
                        title: '',
                        icon: 'success',
                        duration: 2000,
                      })
                      install
                    })
                    .catch(err => {
                      uni.showToast({
                        title: '',
                        icon: 'none',
                        duration: 2000,
                      })
                    })
                } else if (res.cancel) {
                  // 
                  console.log('')
                  // 
                  setStorageSync('version', res.version)
                }
              },
            })
          }
        })
        .catch(err => {
          console.log(':', err)
        })
    },
  },
}
